import React from 'react';
import { Tabs,  message } from 'antd';
import { inject, observer } from 'mobx-react';  
import MyScroll from 'react-custom-scrollbars';//滚动条  
import './right-memu.less';  
const TabPane = Tabs.TabPane;

message.config({
	top: 300 
}); 

// 菜品列表组件
function Dishes({ discontinuedStore, dishes, handleClick }) { 
	return <li onClick={handleClick}>
            <div className="dishes-info" >{dishes.productName} {dishes.productType === 1 && <div className="taocan">套餐</div>} </div>
            <div className="dishes-price">¥{dishes.price} /份</div> 
        </li>  
}


@inject('discontinuedStore') @observer
class RightMemu extends React.Component{

    constructor(props, context) {
		super(props, context);  
        this.state={recordValue:''}; 
        
	}



    componentDidMount() {  
         let discontinuedStore=this.props.discontinuedStore; 
         discontinuedStore.getFirstCategoryList();  //执行查询沽清列表  0是代表查询全部的  
    }

  

  
    
    //menuID：菜单ID  
    clickDishes(dishes) {   
         let discontinuedStore=this.props.discontinuedStore; 
        discontinuedStore.addHaltsSales(dishes.menuID);


         
    }

    //获取搜索的内容
    reachValue=(e)=>{
            var value = e.target.value;
            this.setState({recordValue:value});
    }
  
    render(){ 
        let discontinuedStore = this.props.discontinuedStore; 
        return (
            <div id="right-memu">
                <div className="dishes-category">
                    <Tabs   activeKey={discontinuedStore.firstCategoryID+""} 
                            onTabClick={(categoryID) => {   
                                    discontinuedStore.getSecondCategoryList(categoryID);     
                            }}>
                            {discontinuedStore.firstCategoryList.map((category) => {
                                return <TabPane tab={category.categoryName} key={category.categoryID} onClick={this.firstmemu}></TabPane>
                            })}
                           
                    </Tabs>
                </div>

                <div className="dishes-nav">
                    <div className='nav-tabs'>
                        <Tabs   activeKey={discontinuedStore.secondCategoryID+""} 
                                onTabClick={(categoryID) => {  
                                        discontinuedStore.getDishesList({categoryID});   
                                }}>

                                 {discontinuedStore.secondCategoryList.map((category) => {
                                    return <TabPane tab={category.categoryName} key={category.categoryID} onClick={this.secondmemu}></TabPane>
                                })}
                                
                        </Tabs>
                    </div>
                    <div className='nav-search'>
                        <input  type="text" placeholder="搜索菜品"  onKeyUp={this.reachValue}
                                onChange={(e) =>{  
                                        discontinuedStore.getDishesList({categoryID:discontinuedStore.secondCategoryID,searchCode: e.target.value});  
						        }} />
                        <i className="iconfont icon-order_btn_search"></i>
                    </div>
                </div>
            
               <ul className="dishes-list"> 
                    <MyScroll>
                         {discontinuedStore.dishesList.map((dishes,index) => {
                           return <Dishes key={index} discontinuedStore={discontinuedStore} dishes={dishes} handleClick={this.clickDishes.bind(this, dishes)}  />
                        })} 
                    </MyScroll>
                   
                </ul>
               <div className="dishes-pagination">
                    <div>上一页</div>
                    <div>下一页</div>
                </div> 
            </div>
           
        );
    }
}

export default RightMemu;



